import { setUITheme } from '../actions/config'

function setFromCookie(store) {
  const cookie = document.cookie
  if (cookie) {
    for (const c of cookie.split(';')) {
      const [k, v] = c.split('=')
      if (k === 'ui-theme') {
        console.log(`ui-theme set to '${v}'`)
        store.dispatch(setUITheme(v))
        return true
      }
    }
  }
  return false
}

function setTheme(store) {
  if (!setFromCookie(store)) {
    store.dispatch(setUITheme(store.getState().config.uiTheme))
  }
}

export default setTheme