import React from 'react'
import { connect } from 'react-redux'
import { addToDocument } from '../../actions/document'

class GenerateAndAddButton extends React.Component {

  generateAndAdd = () => {
    const {id, generator, defaultParams} = this.props
    if (generator) {
      console.log(`Generating ${id}`)
      try {
        const result = new generator(defaultParams).output()
        this.props.addToDocument(result)
      } catch(e) {
        console.log('failed')
        console.log(e)
      }
    }
  }

  render() {
    return (
      <button
        disabled={this.props.generator === undefined}
        onClick={this.generateAndAdd}
      >{this.props.children}</button>
    )
  }
}


const mapStateToProps = state => ({
  id: state.uistate.generatorSelection,
  generator: state.generators.generators[state.uistate.generatorSelection],
  defaultParams: state.generators.defaultParams[state.uistate.generatorSelection],
})
const mapDispatchToProps = dispatch => ({
  addToDocument: elem => dispatch(addToDocument(elem))
})

export default connect(mapStateToProps, mapDispatchToProps)(GenerateAndAddButton)