function latexpifraction({n, d, s}) {
  if (n === 0) {
    return '0'
  }
  if (d < 0) {
    return latexpifraction({n: -n, d: -d})
  }
  let numer
  if (n === 1) {
    numer = '\\pi'
  } else {
    numer = `${n}\\pi`
  }
  if (s === -1) {
    numer = '-' + numer
  }
  if (d === 1) {
    return numer
  } else {
    return `\\frac{${numer}}{${d}}`
  }
}

