export class ConvertAngle extends QGen {
  static info = {
    name: 'Convert Angle',
    description: 'Convert between degrees and radians'
  }

  static params = {
    conversion: {
      label: 'Convert',
      type: 'select',
      options: ['degrees to radians', 'radians to degrees', 'either'],
    },
    negative: {
      label: 'Negative angles',
      type: 'select',
      options: ['yes', 'no', 'random'],
    }
  }

  static defaultParams = {
    conversion: 'either',
    negative: 'random'
  }

  static options = {
    singleColumn: true
  }

  generate(params) {
    const rd = this.random
    const conversion = rd.choice({
      'degrees to radians': ['d2r'],
      'radians to degrees': ['r2d'],
      'either': ['d2r', 'r2d'],
    }[params.conversion])
    const neg = rd.choice({
      yes: [true],
      no: [false],
      random: [true, false],
    }[params.negative])
    const rotations = rd.choice([0,1,2])
    
    const denom = rd.choice([5,10,10,12,12])
    const numers = range(1, 2*denom-1, true).filter(n => math.gcd(denom, n) === 1)
    
    let numer = rd.choice(numers)
    if (rotations) {
      numer += denom * 2 * rotations
    }

    // don't freak students out with 666
    while (denom === 10 && numer === 37) {
      numer = rd.choice(numers)
      if (rotations) {
        numer += denom * 2 * rotations
      }
    }
    
    const radians = fraction(neg ? -numer : numer, denom)
    const degrees = 180 * radians.s * radians.n / radians.d

    let instructions, question, answer
    if (conversion === 'r2d') {
      instructions = 'Convert the angle from radians to degrees:'
      question = `$$\\displaystyle ${latexpifraction(radians)}$$`
      answer = `$$${degrees}\\degree$$`
    } else {
      instructions = 'Convert the angle from degrees to radians:'
      question = `$$${degrees}\\degree$$`
      answer = `$$${latexpifraction(radians)}$$`
    }

    return {
      instructions,
      question,
      answer,
      //layout: [['instructions', 'question'], ['answer', 'diagram']]
    }

  }
}


export class IdentifyQuadrant extends QGen {
  static info = {
    name: 'Identify Quadrant',
    description: 'Identify the quadrant of an angle given the sign of two trig functions'
  }

  static params = {
    functions: {
      label: 'Functions to use',
      type: 'multiple-select',
      minSelections: 2,
      options: ['sin', 'cos', 'tan', 'csc', 'sec', 'cot'],
    },
    quadrant: {
      label: 'Allowed quadrants',
      type: 'multiple-select',
      minSelections: 1,
      options: [1, 2, 3, 4],
    }
  }

  static defaultParams = {
    functions: {sin: true, cos: true, tan: true, csc: true, sec: true, cot: true},
    quadrant: {1: true, 2: true, 3: true, 4: true},
  }

  static options = {
    singleColumn: true
  }

  generate(params) {
    const rd = this.random
    
    const functions = {
      // funcs  Q1  Q2  Q3  Q4
      sin: [[], 1,  1, -1, -1],
      cos: [[], 1, -1, -1,  1],
      tan: [[], 1, -1,  1, -1],
    }
    for (const f in params.functions) {
      if (params.functions[f]) {
        if (f === 'sin' || f === 'csc') {
          functions.sin[0].push(f)
        } else if (f === 'cos' || f === 'sec') {
          functions.cos[0].push(f)
        } else if (f === 'tan' || f === 'cot') {
          functions.tan[0].push(f)
        }
      }
    }

    const quadrant = rd.choice(Object.keys(params.quadrant).filter(k => params.quadrant[k]))
    const answer = {1: 'I', 2: 'II', 3: 'III', 4: 'IV'}[quadrant]
    const ineq = {[-1]: '< 0', 1: '> 0'}
    const angle = rd.choice(['theta', 'omega', 'alpha', 'beta'])

    const funcChoices = Object.keys(functions).filter(f => functions[f][0].length > 0)
    const funcKeys = rd.sample(funcChoices, 2)
    const funcs = funcKeys.map(f => rd.choice(functions[f][0]))
  
    const clue = i => `\\${funcs[i]}\\${angle} ${ineq[functions[funcKeys[i]][quadrant]]}`
    const question = `If $$${clue(0)}$$ and $$${clue(1)}$$, which quadrant is $$\\${angle}$$ in?`

    return {
      question,
      answer: {
        correct: answer,
        choices: ['I', 'II', 'III', 'IV'],
        correctIndex: quadrant-1,
        listDirection: 'horizontal',
      }
    }
  }
}

export class SolveRightTriangle extends QGen {
  static info = {
    name: 'Solve Right Triangle',
    description: 'Find all missing side and angle measures for a right triangle'
  }

  static params = {
    given: {
      label: 'Givens can be:',
      type: 'multiple-select',
      minSelections: 1,
      options: [
        {value: 'HL', label: 'Hypotenuse-Leg'},
        {value: 'LL', label: 'Leg-Leg'},
        {value: 'HA', label: 'Hypotenuse-Angle'}, 
        {value: 'LA', label: 'Leg-Angle'},
      ],
    },
  }

  static defaultParams = {
    given: {HL: true, LL: true, HA: true, 'LA': true}
  }

  generate(params) {
    const rd = this.random
    const given = rd.choice(Object.keys(params.given).filter(k => params.given[k]))
    
    // Determine givens
    let givens
    if (given === 'HL') {
      if (rd.bool()) {
        givens = ['a', 'c']
      } else {
        givens = ['b', 'c']
      }
    }

    else if (given === 'LL') {
      givens = ['a', 'b']
    }

    else if (given === 'HA') {
      if (rd.bool()) {
        givens = ['c', 'A']
      } else {
        givens = ['c', 'B']
      }
    }

    else if (given === 'LA') {
      if (rd.bool()) {
        if (rd.bool()) {
          givens = ['a', 'A']
        } else {
          givens = ['b', 'B']
        }
      } else {
        if (rd.bool()) {
          givens = ['a', 'B']
        } else {
          givens = ['b', 'A']
        }
      }
    }

    // Generate triangle
    const C = 90
    let a, b, c, A, B

    // Use integer angles
    A = rd.randint(8, 42)
    while (includes([30, 45, 60, 69, 21], A)) {
      A = rd.randint(8, 42)
    }
    B = 90 - A
    if (rd.bool()) {
      A = B
      B = 90 - A
    }

    c = rd.randint(8, 90)
    a = Math.sin(A * Math.PI / 180) * c
    b = Math.sin(B * Math.PI / 180) * c


    // Make sure all givens are integers
    if (given === 'HA') {
      c = Math.ceil(c)
      a = Math.sin(A * Math.PI / 180) * c
      b = Math.sin(B * Math.PI / 180) * c
    } else if (given === 'LA') {
      if (includes(givens, 'a')) {
        a = Math.ceil(a)
        c = a / Math.sin(A * Math.PI / 180)
        b = a / Math.tan(A * Math.PI / 180)
      } else {
        b = Math.ceil(a)
        c = b / Math.sin(B * Math.PI / 180)
        a = b / Math.tan(B * Math.PI / 180)
      }
    } else if (given === 'LL') {
      a = Math.round(a)
      b = Math.round(b)
      c = Math.sqrt(a*a + b*b)
      A = Math.atan(b/a) * 180 / Math.PI
      B = 90 - A
    } else if (given === 'HL') {
      if (includes(givens, 'a')) {
        a = Math.floor(a)
        c = Math.ceil(c)
        b = Math.sqrt(c*c - a*a)
      } else if (includes(givens, 'b')) {
        b = Math.floor(b)
        c = Math.ceil(c)
        a = Math.sqrt(c*c - b*b)
      }
      A = Math.atan(b/a) * 180 / Math.PI
      B = 90 - A
    }

    //const units = rd.choice(['cm', 'km', 'in', 'ft', 'm'])
    
    const values = {
      a: round(a, 2),
      b: round(b, 2),
      c: round(c, 2),
      A: round(A, 1) + '\\degree',
      B: round(B, 1) + '\\degree',
      C: round(C, 1) + '\\degree'
    }
    

    const labels = {C: 'C'}

    let giveninfo = []
    let answer = []
    let answerblanks = []
    for (const k of ['A', 'B', 'C']) {
      if (k === 'C' || includes(givens, k)) {
        giveninfo.push(`$$m\\angle{${k}} = ${values[k]}$$`)
        labels[k] = '$$' + values[k] + '$$'
      } else {
        answerblanks.push(`$$m\\angle{${k}}$$ =`)
        answer.push(`$$${k} = ${values[k]}$$`)
        labels[k] = '$$?$$'
      }
    }
    for (const k of ['a', 'b', 'c']) {
      if (includes(givens, k)) {
        giveninfo.push(`$$${k} = ${values[k]}$$`)
        labels[k] = '$$' + values[k] + '$$'
      } else {
        answerblanks.push(`length $$${k}$$ =`)
        answer.push(`$$${k} = ${values[k]}$$`)
        labels[k] = '$$' + k + '$$'
      }
    }

    const question = [
      `Given that ${giveninfo.slice(0,-1).join(', ')} and ${giveninfo[giveninfo.length-1]},`,
      'determine the measure of all other sides and angles for triangle $${ABC}$$.',
      'The diagram is __not__ to scale.'
    ].join(' ')

    const verts = {
      A: Point.Cartesian(0,0),
      B: Point.Cartesian(4,3),
      C: Point.Cartesian(4,0)
    }
    const triangle = new Polygon(verts.A, verts.C, verts.B)
    const opts = {
      round: 2, 
      markers: '...', 
      labels: ['A', 'C', 'B'].map(l => '$$' + l + '$$'),
      distance: .3
    }
    const AB = new Segment(verts.A, verts.B)
    const AC = new Segment(verts.A, verts.C)
    const BC = new Segment(verts.B, verts.C)
    const labelAt = {
      A: verts.A.add({x: 1, y: .3}),
      B: verts.B.add({x: -.35, y: -.8}),
    }
    const segLabels = [
      BC.plotLabel(labels.a, {where: 'right', distance: .1, anchor: 'W'}),
      AC.plotLabel(labels.b, {where: 'below', distance: .1, anchor: 'N'}),
      AB.plotLabel(labels.c, {where: 'above', distance: .1, anchor: 'S', rotate: 'auto'})
    ]

    const diagram = [
      'CoordinatePlane', {span: "0,0 4,3", axis: false, grid: false, clip: false, height: '1.5in', style: 'geometry'},
      //['Grid', {span: [0,0, 4,3], step: [.25,.25], axis: false}],
      ['AngleMark', {right: true, x: 4, y: 0, initial: "90deg", terminal: "180deg", radius: .4}],
      ...triangle.plot(opts),
      ...segLabels,
      ['Overlay', labelAt.A.toObject(opts), labels.A],
      ['Overlay', labelAt.B.toObject(opts), labels.B],
    ]

    return {
      question,
      diagram,
      answer: {
        correct: answer.join(', '),
        prompt: [
          'AnswerBlanks', {box: true},
          ...answerblanks
        ]
      },
    }
  }
}