import WebFont from 'webfontloader'
import { addFontFamily } from '../actions/config'
import { includes } from 'lodash'

function loadFonts(store) {
  const toLoad = {
    custom: [
      'katex_ams',
      'katex_caligraphic',
      'katex_fraktur',
      'katex_main',
      'katex_math',
      'katex_sansserif',
      'katex_script',
      'katex_size1',
      'katex_size2',
      'katex_size3',
      'katex_size4',
      'katex_typewriter',
      'cmu_bright',
      'cmu_concrete',
      'cmu_sansserif',
      'cmu_serif',
    ],
    google: [
      'IBM Plex Serif', 
      'IBM Plex Sans',
    ],
  }
  const toAdd = [
    'cmu_sansserif', 
    'cmu_serif', 
    'cmu_bright', 
    'IBM Plex Serif',
    'IBM Plex Sans',
  ]

  WebFont.load({
    custom: {
      families: toLoad.custom.map(f => `${f}:n4,i4,n7,i7`),
      urls: ['./stylesheets/styles.global.css']
    },
    google: {families: toLoad.google.map(f => f + ':n4,i4,n7,i7')},
    fontactive: (family) => {
      if (includes(toAdd, family)) {
        console.log(`add font ${family} successful`)
        store.dispatch(addFontFamily(family))
      }
    },
    fontinactive: (family) => console.error(`load font ${family} failed`),
  })
}

export default loadFonts