import React from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import styles from './styles.module.css'
import { updateGraphStyle } from '../../actions/style'
import { setAutoPagination, setPresentationZoom } from '../../actions/config'
import { openModal } from '../../actions/uistate'
import SingleQuestionView from './SingleQuestionView'

const STORED_QUESTIONS = 10 // before and after current

class PracticeMode extends React.Component {
  static propTypes = {
    generator: PropTypes.func.isRequired,
    updateGraphStyle: PropTypes.func,
    setAutoPagination: PropTypes.func,
  }

  constructor(props) {
    super(props)
    this.state = {
      generated: [],
      index: 0,
      showAnswer: false,
      scaleFactor: props.scaleFactor || 1.2,
    }
  }

  componentDidMount() {
    this.props.setAutoPagination(false)
    this.setScaleFactor(this.state.scaleFactor)
    this.setState({generated: [this.generate()]})
  }
  close = () => {
    this.props.setPresentationScaleFactor(this.state.scaleFactor)
    this.props.setGraphScaleFactor(1)
    this.props.setAutoPagination(true)
    this.props.onRequestClose()
  }

  generate = () => {
    this.setState({showAnswer: false})
    try {
      const {generator, defaultParams} = this.props
      const question = new generator(defaultParams)
      return question.output()
    } catch(e) {
      console.error(e)
    }
  }

  openOptions = () => {
    this.props.openOptions()
  }

  current() {
    return this.state.generated[this.state.index]
  }

  next = () => {
    let index = this.state.index + 1
    if (index > STORED_QUESTIONS) {
      let generated = this.state.generated
      while (index > STORED_QUESTIONS) {
        generated = [...generated.slice(1), this.generate()]
        index -= 1
      }
      this.setState({index, generated, showAnswer: false})
    }
    else if (index >= this.state.generated.length) {
      let generated = [...this.state.generated]
      while (index >= generated.length) {
        generated.push(this.generate())
      }
      this.setState({index, generated, showAnswer: false})
    }
    else {
      this.setState({index, showAnswer: false})
    }
  }

  previous = () => {
    let index = this.state.index - 1
    if (index < 0) {
      let generated = this.state.generated
      while (index < 0) {
        generated = [this.generate(), ...generated.slice(0,STORED_QUESTIONS)]
        index += 1
      }
      this.setState({index, generated, showAnswer: false})
    } else {
      this.setState({index, showAnswer: false})
    }
  }

  showAnswer = () => this.setState({showAnswer: true})
  hideAnswer = () => this.setState({showAnswer: false})
  setScaleFactor(scaleFactor) {
    this.props.setGraphScaleFactor(scaleFactor)
    this.setState({scaleFactor})
  }
  changeScaleFactor = evt => this.setScaleFactor(evt.target.value)

  renderAnswerButton() {
    if (this.state.showAnswer) {
      return (
        <button className={styles.NavButton} onClick={this.hideAnswer}>
          Show Question
        </button>
      )
    } else {
      return (
        <button className={styles.NavButton} onClick={this.showAnswer}>
          Show Answer
        </button>
      )
    }
  }

  onKeyDown = evt => {
    if (evt.key === 'ArrowRight') {
      evt.preventDefault()
      this.next()
    } else if (evt.key === 'ArrowLeft') {
      evt.preventDefault()
      this.previous()
    } else {
      return false
    }
  }
  
  render() {
    const content = this.current()
    return (
      <div className={styles.PresentationView}
        tabIndex="1"
        onKeyDown={this.onKeyDown}
      >
        <div className={styles.Nav}>
          <div className={styles.ArrowButtonContainer}>
            <a className={styles.ArrowButton + ' ' + styles.ArrowButtonLeft}
              onClick={this.previous}/>
            <a className={styles.ArrowButton + ' ' + styles.ArrowButtonRight}
              onClick={this.next}/>
          </div> 
          {this.renderAnswerButton()}
          <button className={styles.NavButton} 
            onClick={this.openOptions} 
            disabled={!this.props.defaultParams}
          >Options</button>
          <div>
            Scale: 
            <input type="number" min=".2" step=".2"
              value={this.state.scaleFactor} 
              autoFocus={true}
              onChange={this.changeScaleFactor}/>     
          </div>
          <button className={styles.NavButton} onClick={this.close}>Close</button>
          
        </div>
        <div className={styles.Slide}>
          {content ? (
            <SingleQuestionView 
              content={content}
              scaleFactor={this.state.scaleFactor}
              showAnswer={this.state.showAnswer}
            />
          ) : null}
        </div>
      </div>
    )
  }
}

const mapStateToProps = state => ({
  //generator: state.generators.generator[state.uistate.generatorSelection],
  //defaultParams: state.generators.defaultParams[state.uistate.generatorSelection],
  //graphScaleFactor: state.style.graph.scaleFactor,
  scaleFactor: state.config.presentationZoom / 100,
})
const mapDispatchToProps = dispatch => ({
  setAutoPagination: bool => dispatch(setAutoPagination(bool)),
  setGraphScaleFactor: scaleFactor => dispatch(updateGraphStyle({scaleFactor})),
  openOptions: () => dispatch(openModal('generatorParams')),
  setPresentationScaleFactor: scaleFactor => dispatch(setPresentationZoom(scaleFactor*100)),
})

export default connect(mapStateToProps, mapDispatchToProps)(PracticeMode)