import React from 'react';
import { connect } from 'react-redux';
import ReactModal from 'react-modal';
import { Preview, SideBar, StatusBar, TitleBar, TopBar, Wrapper } from './AppLayout'
import Document from './Document/Document'
import CustomQuestion from './CustomQuestion'
import AppModals from './Modals'
import { openModal, closeModal } from '../actions/uistate'
import { CheckStateButton } from './Debug'
import {
  AllowEditingToggle,
  AnswerKeyToggle,
  FixPagination,
  FontFamily,
  FontSize,
  GUImode,
  PreviewZoom,
  StartNumbering
} from './Settings'
import {
  removeLast,
  clearAll,
  resetDocument,
} from '../actions/document'
import { resetStyle } from '../actions/style'
import {
  AddButton,
  QuestionSearch,
} from './GeneratorMenu'

import '../stylesheets/styles.global.css'


ReactModal.setAppElement('#root')
Object.assign(ReactModal.defaultStyles.content, {
  borderRadius: '4px',
  borderColor: 'var(--ui-border-1, #999)',
  background: 'var(--ui-bg-1, #eee)',
  padding: '5mm',
  top: '10px',
  left: '10px',
  right: '10px',
  bottom: '10px',
})
Object.assign(ReactModal.defaultStyles.overlay, {
  background: 'rgba(10,10,10,0.3)',
})


class AppWrapper extends React.Component {
  static defaultParams = {
    statusBar: false,
    titleBar: false,
  }

  constructor(props) {
    super(props)
    this.state = {
      modal: 'none',
    }
  }

  addToDoc = elem => this.props.addToDocument(elem)

  newDoc = () => {
    this.props.resetDocument()
    this.props.resetStyle()
  }

  closeModal = () => this.props.closeModal()
  openCustomQuestion = () => this.props.openModal('customQuestion')
  openSettings = () => this.props.openModal('settings')
  openImport = () => this.props.openModal('import')
  openSave = () => this.props.openModal('save')
  openPresentation = () => this.props.openModal('presentation')
  openPractice = () => this.props.openModal('practice')
  openNotes = () => this.props.openModal('notes')
  openGeneratorParams = () => this.props.openModal('generatorParams')

  render() {
    const toplabel = {margin: '0 0 0 0.5em'}
    return (
      <Wrapper statusBar={this.props.statusBar ? true : false} titleBar={false}>
        {this.props.titleBar ? (
          <TitleBar />
        ) : null}
        {this.props.statusBar ? (
          <StatusBar><span>{this.props.statusBar}</span></StatusBar>
        ) : null }
        <TopBar>
          <button onClick={this.newDoc}>New</button>
          <button onClick={this.openImport}>Import</button>
          <button onClick={this.openSave}>Save</button>
          <button onClick={window.print}>Print/PDF</button>
          <button onClick={this.openSettings}>Settings</button>
          <label htmlFor="show-answer-key-checkbox" style={toplabel}>Answer Key:</label><AnswerKeyToggle />
          <span style={toplabel}>Start Numbering:</span><StartNumbering />
          <span style={toplabel}>Font:</span><FontFamily/><FontSize />
          <span style={toplabel}>Zoom:</span><PreviewZoom defaultValue="100"/>
          <label htmlFor="allow-editing-checkbox" style={toplabel}>Allow Editing:</label><AllowEditingToggle />
          <div style={{width: '3mm'}} />
          <FixPagination/>
        </TopBar>
        <Preview>
          <Document/>
        </Preview>
        <SideBar>
          <QuestionSearch/>
          <AddButton>Add</AddButton>
          <button onClick={this.openGeneratorParams} 
            disabled={!this.props.generatorParams}>Change Options</button>
          <button onClick={this.props.removeLast} 
            disabled={!this.props.hasContent}>Remove Last</button>
          <button onClick={this.props.clearAll} 
            disabled={!this.props.hasContent}>Clear All</button>
          <div style={{height: '3mm'}}/>
          <button onClick={this.openCustomQuestion}>Custom Question</button>
          <div style={{height: '3mm'}}/>
          <button onClick={this.openPresentation} 
            disabled={!this.props.hasContent}>Slide View</button>
          <button onClick={this.openPractice} 
            disabled={!this.props.generatorSelected}>Practice</button>
          
          <div style={{margin: 'auto'}}></div>

          <GUImode/>
          <button onClick={this.openNotes}>Notes</button>
          <CheckStateButton>Log State</CheckStateButton>
        </SideBar>

        <AppModals />
      </Wrapper>
    )

  }
}

const mapStateToProps = state => {
  const generatorId = state.uistate.generatorSelection
  let generatorSelected = false, generatorParams = false
  if (generatorId) {
    generatorSelected = true
    if (state.generators.params[generatorId]) {
      generatorParams = true
    }
  }
  return ({
    fontFamilyUI: state.config.fontFamilyUI,
    hasContent: state.document.order.length > 0,
    generatorSelected,
    generatorParams,
  })
}

const mapDispatchToProps = dispatch => ({
  removeLast: () => dispatch(removeLast()),
  clearAll: () => dispatch(clearAll()),
  resetDocument: () => dispatch(resetDocument()),
  resetStyle: () => dispatch(resetStyle()),
  openModal: modal => dispatch(openModal(modal)),
  closeModal: () => dispatch(closeModal()),
})

export default connect(mapStateToProps, mapDispatchToProps)(AppWrapper)