const dict19 = {
  0: 'zero',
  1: 'one',
  2: 'two',
  3: 'three',
  4: 'four',
  5: 'five',
  6: 'six',
  7: 'seven',
  8: 'eight',
  9: 'nine',
  10: 'ten',
  11: 'eleven',
  12: 'twelve',
  13: 'thirteen',
  14: 'fourteen',
  15: 'fifteen',
  16: 'sixteen',
  17: 'seventeen',
  18: 'eighteen',
  19: 'nineteen',
}

const dictTens = {
  2: 'twenty',
  3: 'thirty',
  4: 'fourty',
  5: 'fifty',
  6: 'sixty',
  7: 'seventy',
  8: 'eighty',
  9: 'ninety',
}

const dictPowers = {
  3:'thousand',
  6:'million',
  9:'billion',
  12:'trillion',
  15:'quadrillion',
  18:'quintillion',
  21:'sextillion',
  24:'septillion',
  27:'octillion',
  30:'nonillion',
  33:'decillion',
  36:'undecillion',
  39:'duodecillion',
  42:'tredecillion',
  45:'quattuordecillion',
  48:'quindecillion',
  51:'sexdecillion',
  54:'septendecillion',
  57:'octodecillion',
  60:'novemdecillion',
  63:'vigintillion',
}

function divmod(n, d) {
  return [Math.floor(n/d), n % d]
}

export function sayinteger(n) {
  if (n < 0) {
    return 'negative ' + sayinteger(-n)
  } else if (n < 20) {
    return dict19[n]
  } else if (n < 100) {
    const [a, b] = divmod(n, 10)
    if (b === 0) {
      return dictTens[a]
    } else {
      return dictTens[a] + '-' + dict19[b]
    }
  } else if (n < 1000) {
    const [a, b] = divmod(n, 100)
    if (b === 0) {
      return dict19[a] + ' hundred'
    } else {
      return dict19[a] + ' hundred ' + sayinteger(b)
    }
  }
  for (const k of Object.keys(dictPowers).sort((a,b) => a-b)) {
    if (n < Math.pow(10,k+3)) {
      const [a, b] = divmod(n, Math.pow(10,k))
      if (b === 0) {
        return sayinteger(a) + ' ' + dictPowers[k]
      } else {
        return sayinteger(a) + ' ' + dictPowers[k] + ', ' + sayinteger(b)
      }
    }
  }
  return n.toString()
}

export function saynth(n) {
  const replacer = {
    one: 'first',
    two: 'second',
    three: 'third',
    five: 'fifth',
    eight: 'eighth',
    nine: 'ninth',
    twelve: 'twelfth',
  }
  for (const ten of [20, 30, 40, 50, 60, 70, 80, 90]) {
    const s = sayinteger(ten)
    replacer[s] = s.replace('ty', 'tieth')
  }
  if (n === 0) {
    return 'zeroeth'
  }
  let words = sayinteger(n).split(' ')
  let lastword = words[words.length-1].split('-')
  let ending = lastword[lastword.length-1]
  if (replacer[ending]) {
    ending = replacer[ending]
  } else {
    ending += 'th'
  }
  lastword[lastword.length-1] = ending
  words[words.length-1] = lastword.join('-')
  return words.join(' ')
}
