import React from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import { fixPagination, decreaseHiddenCount } from '../../actions/document'

class FixPagination extends React.Component {
  static propTypes = {
    hidden: PropTypes.number.isRequired,
    decreaseHiddenCount: PropTypes.func.isRequired,
    fixPagination: PropTypes.func.isRequired,
  }

  check() {
    setTimeout(() => {
      if (this.props.hidden > 0) {
        this.props.decreaseHiddenCount()
      }
    }, 20)
  }

  componentDidMount() {
    this.check()
  }

  componentDidUpdate() {
    this.check()
  }

  render() {
    return (
      <button onClick={this.props.fixPagination}>Fix Pagination</button>
    )
  }
}

const mapStateToProps = state => ({
  hidden: state.document.numberHidden,
})

const mapDispatchToProps = dispatch => ({
  decreaseHiddenCount: () => dispatch(decreaseHiddenCount()),
  fixPagination: () => dispatch(fixPagination()),
})

export default connect(mapStateToProps, mapDispatchToProps)(FixPagination)