import React from 'react'
import { Clip, Circle, Rect, Transform, Style } from '.'
import { includes } from 'lodash'


function determineRadius(props) {
  let {minRadius, maxRadius} = props
  if (minRadius === undefined) {
    minRadius = maxRadius * 0.5
  }
  if (maxRadius === undefined) {
    maxRadius = minRadius * 2
  }
  const angleSize = props.terminal - props.initial
  if (angleSize < 90) {
    const diff = maxRadius - minRadius
    return minRadius + diff*(90-angleSize)/90
  } else {
    return minRadius
  }
}

function parseCoords(coord) {
  if (coord) {
    if (typeof coord === 'string') {
      const [x, y] = coord.split(',').map(n => +n)
      return {x, y}
    }
    if (Array.isArray(coord)) {
      const [x, y] = coord
      return {x, y}
    }
    const {x, y} = coord
    return {x, y}
  }
}

function determineVertex(props) {
  if (props.vertex) return parseCoords(props.vertex)
  if (props.coords) return parseCoords(props.coords)
  if ((props.x !== undefined) && (props.y !== undefined)) {
    const {x, y} = props
    return {x, y}
  }
}

function parseAngleSide(vertex, side) {
  if (typeof side === 'number') {
    const degrees = side
    const radians = degrees * Math.PI / 180
    return {radians, degrees}
  } else if (typeof side === 'string') {
    if (includes(side, ',')) {
      const [x, y] = side.split(',').map(n => +n)
      const dx = x - vertex.x
      const dy = y - vertex.y
      const radians = Math.atan2(dy, dx)
      const degrees = radians * 180 / Math.PI
      if (degrees >= 360) {
        return {
          degrees: degrees - 360,
          radians: radians - 2*Math.PI,
        }
      } else {
        return {degrees, radians}
      }
    } else {
      const degrees = parseFloat(side)
      const radians = degrees * Math.PI / 180
      return {radians, degrees}
    }
  } else {
    const {x, y} = parseCoords(side)
    const dx = x - vertex.x
    const dy = y - vertex.y
    const radians = Math.atan2(dy, dx)
    const degrees = radians * 180 / Math.PI
    if (degrees >= 360) {
      return {
        degrees: degrees - 360,
        radians: radians - 2*Math.PI,
      }
    } else {
      return {degrees, radians}
    }
  }
}

function determineAngles(vertex, props) {
  let {measure, initial, terminal} = props
  if (initial === undefined) {
    const term = parseAngleSide(vertex, terminal)
    const measureDeg = parseFloat(measure)
    const initDeg = term.degrees - measureDeg
    const initRad = initDeg * Math.PI / 180
    return {terminal: term, initial: {degrees: initDeg, radians: initRad}, measure: {degrees: measureDeg}}
  }
  if (terminal === undefined) {
    const init = parseAngleSide(vertex, initial)
    const measureDeg = parseFloat(measure)
    const termDeg = init.degrees + measureDeg
    const termRad = termDeg * Math.PI / 180
    return {initial: init, terminal: {degrees: termDeg, radians: termRad}, measure: {degrees: measureDeg}}
  }
  const init = parseAngleSide(vertex, initial)
  let term = parseAngleSide(vertex, terminal)
  if (term.degrees < init.degrees) {
    term = {degrees: term.degrees + 360, radians: term.radians + 2*Math.PI}
  }
  const meas = {degrees: term.degrees - init.degrees}
  return {initial: init, terminal: term, measure: meas}
}

const RightAngleMark = props => {
  const {x, y} = determineVertex(props)
  const radius = props.radius || determineRadius(props)
  const {initial, terminal, measure} = determineAngles({x, y}, props)
  const rotate = measure.degrees === 90 ? initial.degrees : initial.degrees + (measure.degrees - 90)/2
  const vec1 = {
    x: Math.cos(initial.radians) * radius, 
    y: Math.sin(initial.radians) * radius,
  }
  const vec2 = {
    x: Math.cos(terminal.radians) * radius, 
    y: Math.sin(terminal.radians) * radius,
  }
  const clipPoints = [
    [x,y],
    [x + 2*vec1.x, y + 2*vec1.y],
    [x + 2*vec1.x + 2*vec2.x, y + 2*vec1.y + 2*vec2.y],
    [x + 2*vec2.x, y + 2*vec2.y]
  ]

  return (
    <Clip shape="Path" points={clipPoints}>
      <Transform type="rotate" a={rotate} x={x} y={y}>
        <Rect x={x-radius} y={y-radius} width={2*radius} height={2*radius} style="anglemark anglemark-right"/>
      </Transform>
    </Clip>
  )
}

const RoundAngleMark = props => {
  const {x, y} = determineVertex(props)
  const radius = props.radius || determineRadius(props)
  const {initial, terminal, measure} = determineAngles({x, y}, props)
  const arcStart = {
    x: x + Math.cos(initial.radians) * radius * 2, 
    y: y + Math.sin(initial.radians) * radius * 2,
  }
  const arcEnd = {
    x: x + Math.cos(terminal.radians) * radius * 2,
    y: y + Math.sin(terminal.radians) * radius * 2,
  }
  const d = [
    `M${x},${y}`,
    `L ${arcStart.x},${arcStart.y}`,
    `A ${radius*2} ${radius*2} 0 ${measure.degrees > 180 ? 1 : 0} 1`,
    `${arcEnd.x},${arcEnd.y} z`,
  ].join(' ')

  const style = 'anglemark'

  return (
    <Clip shape="Path" d={d}>
      <Circle cx={x} cy={y} r={radius} style={style}/>
    </Clip>
 )
}

const CongruentAngleMarks = props => {
  const {x, y} = determineVertex(props)
  const {initial, terminal, measure} = determineAngles({x, y}, props)
  const {radius, congruent} = props
  const r = Array.isArray(radius) ? Math.max(...radius) : radius
  const arcStart = {
    x: x + Math.cos(initial.radians) * r * 2, 
    y: y + Math.sin(initial.radians) * r * 2,
  }
  const arcEnd = {
    x: x + Math.cos(terminal.radians) * r * 2,
    y: y + Math.sin(terminal.radians) * r * 2,
  }
  const d = [
    `M${x},${y}`,
    `L ${arcStart.x},${arcStart.y}`,
    `A ${r*2} ${r*2} 0 ${measure.degrees > 180 ? 1 : 0} 1`,
    `${arcEnd.x},${arcEnd.y} z`,
  ].join(' ')

  const radii = Array.isArray(radius) ? radius : [radius]

  const style = 'anglemark-congruent'
  
  return (
    <Clip shape="Path" d={d}>
      <Style name={style}>
        {radii.slice(0,congruent).map(v => <Circle key={v} cx={x} cy={y} r={v}/>)}
      </Style>
    </Clip>
  )
}

const AngleMark = props => {
  if (props.right || props.square) return React.createElement(RightAngleMark, props)
  if (props.congruent) return React.createElement(CongruentAngleMarks, props)
  return React.createElement(RoundAngleMark, props)
}

export { AngleMark }
export default AngleMark