import React from 'react'
import { connect } from 'react-redux'
//import questionbank from './questionbank'
import { includes } from 'lodash'
import styles from './styles.module.css'
import { addToDocument } from '../../actions/document'
import { PracticeModal } from '../Modals'
import { updateGeneratorSelection } from '../../actions/uistate'


function hasKwd(item, kwd) {
  for (const v of [...item.name.split(' '), ...(item.tags || [])].map(t => t.toLowerCase())) {
    if (includes(v, kwd.replace(',', ''))) {
      return true
    }
  }
  return false
}

function checkForKwds(item, kwds=[]) {
  for (const k of kwds.map(k => k.toLowerCase())) {
    if (!hasKwd(item, k)) {
      return false
    }
  }
  // item contains all keywords
  return true
}

function sortQuestionType(a, b) {
  if ((a.sort || 0) < (b.sort || 0)) return -1
  if ((a.sort || 0) > (b.sort || 0)) return 1
  if (a.prefix < b.prefix) return -1
  if (a.prefix > b.prefix) return 1
  if (a.name < b.name) return -1
  if (a.name > b.name) return 1
  return 0
}

class QuestionSearch extends React.Component {

  constructor(props) {
    super(props)
    this.state = {
      input: '',
      kwds: [],
      selection: null,
      modal: 'none',
    }
  }

  clearSearch = () => {
    this.setState({ input: '', kwds: [], selection: null})
  }

  updateSearch = evt => {
    const input = evt.target.value
    const kwds = input.split(' ').map(k => k.toLowerCase())
    this.setState({ input, kwds, selection: null })
  }

  setSelection(id) {
    this.props.updateGeneratorSelection(id)
  }

  generateAndAdd = () => {
    const {currentSelection, generator, defaultParams} = this.props
    if (generator) {
      console.log(`Generating ${currentSelection}`)
      try {
        const result = new generator(defaultParams).output()
        this.props.addToDocument(result)
      } catch(e) {
        console.log('failed')
        console.log(e)
      }
    }
  }

  renderResults() {
    const results = (
      Object.keys(this.props.info)
        .map(k => ({id: k, ...this.props.info[k]}))
        .filter(q => checkForKwds(q, this.state.kwds))
        .sort(sortQuestionType)
    )
    const className = styles.ResultContainer + ' ui-input'
    if (results.length) {
      return (
        <div className={className}>
          {results.map(r => (
            <div key={r.id} 
              className={styles.SearchResult + (r.id === this.props.currentSelection ? ' ' + styles.Selected : '')}
              onClick={() => this.setSelection(r.id)}
              onDoubleClick={this.generateAndAdd}
              title={r.name + (r.description ? `\n${r.description}` : '')}
            >
              {(r.prefix || '') + r.name}
            </div>
          ))}
        </div>
      )
    } else {
      return (
        <div className={className}>
          <span className={styles.SearchResultNone + ' ' + styles.SearchResult}>No results</span>
        </div>
      )
    }
  }

  render() {
    return (
      <>
        <input type="search"
          onChange={this.updateSearch}
          value={this.state.input}
          onDoubleClick={this.clearSearch}
          placeholder="filter"
        />
        {this.renderResults()}
      </>
    )
  }
}

const mapStateToProps = state => {
  const currentSelection = state.uistate.generatorSelection
  return ({
    currentSelection,
    info: state.generators.info,
    generator: state.generators.generators[currentSelection],
    defaultParams: state.generators.defaultParams[currentSelection],
  })
}

const mapDispatchToProps = dispatch => ({
  addToDocument: q => dispatch(addToDocument(q)),
  updateGeneratorSelection: id => dispatch(updateGeneratorSelection(id)),
})


export default connect(mapStateToProps, mapDispatchToProps)(QuestionSearch)