import React from 'react'

import { RenderJson, renderJson } from '../../../renderJson'

export const PreviewXYPlot = () => {
  
  
  const vasymptotes = [-3]
  const holes = [-5]
  const hasymptotes = [2]

  //const f = x => 2*Math.sin(x*Math.PI/4) + 7
  //const g = x => (x+2)*(x-8)/2 + (x+2)/5 + 5

  const f = x => (x-2)*(x-7) - 3 + (x/3)
  const g = x => -(x-2)*(x-7) - 3 + (x/3)


  const regionTop = []
  const regionBottom = []
  const fpoints = []
  const gpoints = []
  const step = 0.5

  let x = -5
  while (x <= 3) {
    const fpt = ({x, y: f(x)})
    const gpt = ({x, y: g(x)})
    fpoints.push(fpt)
    gpoints.push(gpt)
    x += step
  }

  while (x <= 6) {
    const fpt = ({x, y: f(x)})
    const gpt = ({x, y: g(x)})
    fpoints.push(fpt)
    gpoints.push(gpt)
    x += 0.02
  }

  while (x <= 11) {
    const fpt = ({x, y: f(x)})
    const gpt = ({x, y: g(x)})
    fpoints.push(fpt)
    gpoints.push(gpt)
    x += step
  }

  const json = [
    'CoordinatePlane', {span: [-10,-10,10,10], height: '3in', style: 'none'},
    [
      'Style', {name: 'asymptote'},
      ...vasymptotes.map(v => ['Path', {points: [[v, -12], [v, 12]]}]),
      ...hasymptotes.map(v => ['Path', {points: [[-12, v], [12, v]]}])
    ],
    [
      'Style', {name: 'function primary'},
      ['Path', {points: fpoints, fill: 'solid'}],
      [
        'Transform', {type: 'translate', y: 2},
        [
          'RationalFunction', {
            numerLC: 2,
            asymptotes: vasymptotes,
            holes: holes,
            domain: [-11,-3],
            renderAsymptotes: false,
          }
        ],
      ],
      ['Point', {x: -4, y: 0}],
    ],
    ['Overlay', {x: -9, y: 7, anchor: 'W'}, '$$f(x) = \\frac{1}{x-2}+2$$'],
    [
      'Style', {name: 'function secondary'},
      ['Path', {points: gpoints, fill: 'solid'}],
      
      ['Point', {x: 2, y: f(2)}],
      ['Point', {x: 7, y: f(7)}],
      
    ]
  ]
  return renderJson(json)
}

export const PreviewGeom = () => {

  const poly= [
    [8,1], [18,1], [18,8], [14,8]
  ]
  const ray = [[2,12], [7,13]]
  const line = [[8,17], [2,18]]
  const seg = [[7,15], [2,15]]
  const parallelogram = [[11,18], [17,19], [15,13], [9,12]]
  const parAngles = {
    0: {
      vertex: parallelogram[0],
      initial: parallelogram[3],
      terminal: parallelogram[1],
    },
    1: {
      vertex: parallelogram[1],
      initial: parallelogram[0],
      terminal: parallelogram[2],
    },
    2: {
      vertex: parallelogram[2],
      initial: parallelogram[1],
      terminal: parallelogram[3],
    },
    3: {
      vertex: parallelogram[3],
      initial: parallelogram[2],
      terminal: parallelogram[0],
    },
  }

  const json = [
    'CoordinatePlane', {span: [0,0,20,20], height: '3in', style: 'geometry'},
    //['AngleMark', {right: true, vertex: poly[1], initial: poly[2], terminal: poly[0], radius: 1.5}],
    ['Path', {points: ray, markers: ".->"}],
    ['Path', {points: line, markers: "<->"}],
    ['Path', {points: seg, markers: ".-."}],
    ['AngleMark', {right: true, vertex: poly[2], initial: poly[3], terminal: poly[1], radius: 1.5}],
    ['Polygon', {points: poly, markers: "..."}],
    ['AngleMark', {...parAngles[0], radius: [.5, .8, 1.1], congruent: 2}],
    ['AngleMark', {...parAngles[2], radius: [.5, .8, 1.1], congruent: 2}],
    ['AngleMark', {...parAngles[1], radius: [.5, .8, 1.1], congruent: 3}],
    ['AngleMark', {...parAngles[3], radius: [.5, .8, 1.1], congruent: 3}],
    ['Polygon', {points: parallelogram, markers: "..."}],
  ]

  return renderJson(json)

}