import {
  ADD_FONTFAMILY,
  SET_ALLOW_EDITING,
  SET_FONTFAMILY_UI,
  SET_SHOW_ANSWERKEY,
  SET_UI_THEME,
  SET_DEBUG_VIEW,
  SET_FILENAME,
  SET_AUTO_PAGINATION,
  SET_PRESENTATION_ZOOM,
} from '../actions/config'

const initialState = {
  //titleBarStyle: 'default',
  fontFamilies: ['sans-serif', 'serif'],
  fontFamilyUI: 'select',
  showAnswerKey: true,
  allowEditing: true,
  uiTheme: 'light',
  debugView: false,
  autoPaginate: true,
  presentationZoom: 120,
}

const config = function(state=initialState, action) {
  switch(action.type) {
    case ADD_FONTFAMILY:
      return {
        ...state, 
        fontFamilies: [...state.fontFamilies.filter(f => f !== action.payload), action.payload],
      }
    case SET_ALLOW_EDITING:
      return {...state, allowEditing: action.payload}
    case SET_FONTFAMILY_UI:
      return {...state, fontFamilyUI: action.payload}
    case SET_SHOW_ANSWERKEY:
      return {...state, showAnswerKey: action.payload}
    case SET_UI_THEME:
      let date = new Date()
      date.setTime(date.getTime() + 1000 * 3600)
      document.cookie = `ui-theme=${action.payload};expires=${date.toUTCString()}`
      return {...state, uiTheme: action.payload}
    case SET_DEBUG_VIEW:
      return {...state, debugView: action.payload}
    case SET_FILENAME:
      return {...state, filename: action.payload}
    case SET_AUTO_PAGINATION:
      return {...state, autoPaginate: action.payload}
    case SET_PRESENTATION_ZOOM:
      return {...state, presentationZoom: action.payload}
    default:
      return state
  }
}

export default config