import React from 'react'
import { connect } from 'react-redux'
import { closeModal, closePopup } from '../../actions/uistate'

import AddFontPopup from './AddFontPopup'
import CustomQuestionModal from './CustomQuestionModal'
import ImportModal from './ImportModal'
import NotesModal from './NotesModal'
import PracticeModal from './PracticeModal'
import PresentationModal from './PresentationModal'
import SavePopup from './SavePopup'
import SettingsModal from './SettingsModal'
import SetGeneratorDefaultsPopup from './SetGeneratorDefaultsPopup'


export {
  AddFontPopup,
  CustomQuestionModal,
  ImportModal,
  NotesModal,
  PracticeModal,
  PresentationModal,
  SavePopup,
  SetGeneratorDefaultsPopup,
  SettingsModal,
}

// There are two layers - popups will show up on top of modals

export const modalMap = {
  customQuestion: CustomQuestionModal,
  import: ImportModal,
  notes: NotesModal,
  save: SavePopup,
  settings: SettingsModal,
  practice: PracticeModal,
  presentation: PresentationModal,
}

export const popupMap = {
  addFont: AddFontPopup,
  generatorParams: SetGeneratorDefaultsPopup,
}

export const AppModals = props => {
  const {currentModal, currentPopup, closeModal, closePopup} = props
  return (
    <>
      {Object.keys(modalMap).map(k => {
        const Modal = modalMap[k]
        return (
          <Modal key={k} isOpen={currentModal === k} onRequestClose={closeModal}/>
      )})}
      {Object.keys(popupMap).map(k => {
        const Modal = popupMap[k]
        return (
          <Modal key={k} isOpen={currentPopup === k} onRequestClose={closePopup}/>
      )})}
    </>
  )
}

const mapStateToProps = state => ({
  currentModal: state.uistate.modal,
  currentPopup: state.uistate.popup,
})
const mapDispatchToProps = dispatch => ({
  closeModal: () => dispatch(closeModal()),
  closePopup: () => dispatch(closePopup()),
})

export default connect(mapStateToProps, mapDispatchToProps)(AppModals)