import React from 'react'
import { connect } from 'react-redux'
import PropTypes from 'prop-types'
import { AddFontPopup } from '../../Modals'
import { includes, indexOf } from 'lodash'
import { setDocumentFontFamily } from '../../../actions/style'
import { openModal } from '../../../actions/uistate'

const DefaultFonts = [
  /*
  'Alegreya',
  'Alegreya Sans',
  'Arima Madurai',
  'BioRhyme',
  'EB Garamond',
  'Fira Sans',
  //'Gentium Basic',
  //'Handlee',
  'IBM Plex Sans',
  'IBM Plex Serif',
  //'Inconsolata',
  //'Jura',
  'Lora',
  //'Merriweather',
  //'Noticia Text',
  //'Noto Sans',
  'Noto Serif',
  'Old Standard TT',
  'Raleway',
  'Roboto',
  'Roboto Slab',
  'Signika',
  'Source Serif Pro',
  'Ubuntu',*/
  'sans-serif', 
  'serif'
]

const SortToEnd = [
  'sans-serif',
  'serif',
  '__CUSTOM__',
]

const fontLabels = {
  '__CUSTOM__': 'other',
  'cmu_bright': 'CMU Bright',
  'cmu_concrete': 'CMU Concrete',
  'cmu_sansserif': 'CMU Sans',
  'cmu_serif': 'CMU Serif',
  'katex_sansserif': 'KaTeX Sans',
  'katex_main': 'KaTeX Serif',
  'sans-serif': 'sans-serif (browser)',
  'serif': 'serif (browser)'
}

function fontSortByName(a, b) {
  const aName = (fontLabels[a] || a).toUpperCase()
  const bName = (fontLabels[b] || b).toUpperCase()
  if (aName < bName) { return -1 }
  if (aName > bName) { return 1}
  return 0
}

function fontSort(a, b) {
  if (includes(SortToEnd, a)) {
    if (includes(SortToEnd, b)) {
      return indexOf(SortToEnd, a) - indexOf(SortToEnd, b)
    }
    return 1
  }
  if (includes(SortToEnd, b)) {
    return -1
  }
  return fontSortByName(a, b)
}

function renderFontOption(family) {
  if (family) {
    const label = fontLabels[family] || family
    return (
      <option key={`font-${family}`}
        value={family}
        title={family === '__CUSTOM__' ? 'Choose a font not on this list' : ''}
        >{label}</option>
    )
  }
  return <option key={0} value="0" disabled={true}>---</option>
}


//////////////////////////////
//
// COMPONENT
//
////////////////////////////


class FontFamilySelect extends React.Component {
  static propTypes = {
    current: PropTypes.string,
    fontFamilies: PropTypes.arrayOf(PropTypes.string).isRequired,
    setDocumentFontFamily: PropTypes.func.isRequired,
  }

  static defaultProps = {
    fontFamilies: DefaultFonts,
  }

  constructor(props) {
    super(props)
  }

  fontList = () => [
    ...this.props.fontFamilies,
    '__CUSTOM__'
  ].sort(fontSort)

  setFontFamily(fontFamily) {
    this.props.setDocumentFontFamily(fontFamily)
  }

  onChange = (evt) => {
    const value = evt.target.value
    if (value === '__CUSTOM__') {
      this.props.openPopup()
    } else {
      this.setFontFamily(value)
    }
  }

  render() {
    return (
      <select
        value={this.props.current}
        onChange={this.onChange}
        placeholder="Select Font"
      >
        {this.fontList().map((f) => renderFontOption(f))}
      </select>
    )
  }
}

const mapStateToProps = state => ({
  current: state.style.fontFamily,
  fontFamilies: [...state.config.fontFamilies],
})

const mapDispatchToProps = dispatch => ({
  openPopup: () => dispatch(openModal('addFont')),
  setDocumentFontFamily: (family) => dispatch(setDocumentFontFamily(family)),
})

export default connect(mapStateToProps, mapDispatchToProps)(FontFamilySelect)