export const ADD_TO_DOCUMENT = 'ADD_TO_DOCUMENT'
export const CLEAR_ALL = 'CLEAR_ALL'
export const REMOVE_LAST = 'REMOVE_LAST'
export const UPDATE_ELEMENT = 'UPDATE_ELEMENT'
export const UPDATE_HEADER = 'UPDATE_HEADER'
export const DELETE_ELEMENT = 'DELETE_ELEMENT'
export const DELETE_HEADER = 'DELETE_HEADER'
export const SET_DOCUMENT_STARTNUMBERING = 'SET_DOCUMENT_STARTNUMBERING'
export const ADD_PAGEBREAK_BEFORE = 'ADD_PAGEBREAK_BEFORE' // need to come up with a better way to do this
export const CLEAR_PAGEBREAKS = 'CLEAR_PAGEBREAKS'
export const LOAD_DOCUMENT = 'LOAD_DOCUMENT'
export const SET_HIDDEN_COUNT = 'SET_HIDDEN_COUNT'
export const FIX_PAGINATION = 'FIX_PAGINATION'
export const DECREASE_HIDDEN_COUNT = 'DECREASE_HIDDEN_COUNT'
export const APPEND_CONTENT = 'APPEND_CONTENT'
export const RESET_DOCUMENT = 'RESET_DOCUMENT'

let elemID = 0

// Document content

export const addToDocument = (elem) => ({
  type: ADD_TO_DOCUMENT,
  payload: {element: elem, id: ++elemID}
})

export const clearAll = () => {
  elemID = 0
  return ({
    type: CLEAR_ALL
  })
}

export const removeLast = () => ({
  type: REMOVE_LAST,
})

export const updateElement = (idstr, data) => {
  const [type, id] = idstr.split('.')
  if (type === 'header') {
    return { type: UPDATE_HEADER, payload: {id: +id, data} }
  }
  if (type === 'content') {
    return { type: UPDATE_ELEMENT, payload: {id: +id, data} }
  }
}

export const deleteElement = (idstr) => {
  const [type, id] = idstr.split('.')
  if (type === 'header') {
    return { type: DELETE_HEADER, payload: +id }
  }
  if (type === 'content') {
    return { type: DELETE_ELEMENT, payload: +id }
  }
}

export const deleteHeader = (id) => ({
  type: DELETE_HEADER,
  payload: +id,
})

export const loadDocument = doc => {
  const ids = Object.keys(doc.content).map(k => +k).sort((a,b) => a-b)
  elemID = ids[ids.length-1]
  return ({
    type: LOAD_DOCUMENT,
    payload: doc
  })
}

export const resetDocument = () => {
  elemID = 0
  return ({
    type: RESET_DOCUMENT
  })
}

export const appendContent = ({content, order, where='end'}) => {
  const updateOrder = []
  const updateContent = {}
  for (const o of order) {
    const id = ++elemID
    updateOrder.push(id)
    updateContent[id] = content[o]
  }
  return ({
    type: APPEND_CONTENT,
    payload: {content: updateContent, order: updateOrder, where}
  })
}

// Document settings

export const setDocumentStartNumbering = (n) => ({
  type: SET_DOCUMENT_STARTNUMBERING,
  payload: n
})

// Document view

export const addPageBreakBefore = (id) => ({
  type: ADD_PAGEBREAK_BEFORE,
  payload: id
})

export const clearPageBreaks = () => ({
  type: CLEAR_PAGEBREAKS
})

export const setHiddenCount = n => ({
  type: SET_HIDDEN_COUNT,
  payload: n
})

export const decreaseHiddenCount = () => ({
  type: DECREASE_HIDDEN_COUNT,
})

export const fixPagination = () => ({
  type: FIX_PAGINATION
})


