import QGen from './QGen'
import fraction from 'fraction.js'
import { sayinteger, saynth } from './tools/saynumber'
import { round, capitalize } from './tools/handystuff'
import { Point, Vector, Line, Segment, Polygon, Circle, bisectAngle } from './tools/geom2d'

function josephus(n, k) {
  if (n === 1) {
    return 1
  } else {
    return (k + josephus(n-1, k) - 1) % n + 1
  }
}

export class Josephus extends QGen {
  static info = {
    name: 'Josephus',
    description: 'A somewhat morbid counting puzzle.'
  }

  generate() {
    const rd = this.random
    const n = rd.randint(17, 29) // number of prisoners
    const k = rd.randint(7, 14) // kill every kth prisoner
    return {
      instructions: [ 
        `**${capitalize(sayinteger(n))}** prisoners are forced to stand in a line.`,
        'Their captor, a disgraced number theorist, announces that he will begin counting the prisoners beginning at the front of the line.',
        `Each time he reaches the number **${sayinteger(k)}**, he will execute that prisoner and then restart his count with the next prisoner in line.`,
        'When he reaches the end of the line he will move back to the front of the line and continue counting where he left off.',
        `He will continue to do this, each time executing the ${saynth(k)} prisoner, until only one prisoner remains.`,
        'This last remaining prisoner will be set free.',
      ].join(' '),
      question: [
        '*You are one of the prisoners.*',
        'Before he begins counting, your captor gives you a chance to rearrange yourselves.',
        'Where should you stand to make sure you will survive?',
      ].join(' '),
      answer: saynth(josephus(n, k)),
    }
  }
}

export class FinalDigit extends QGen {
  static info = {
    name: 'Final Digit',
  }

  static options = {
    singleColumn: true,
  }

  generate() {
    const rd = this.random
    const base = rd.choice([2,3,7,8])
    const exponent = rd.randint(300000,3000000)

    const answer = {
      2: [6, 2, 4, 8],
      3: [1, 3, 9, 7],
      7: [1, 7, 9, 3],
      8: [6, 8, 4, 2],
    }[base][exponent % 4]

    const exampleExponent = rd.choice({
      2: [4,5,6],
      3: [3,4],
      7: [2,3],
      8: [2,3],
     }[base])
    const exampleResult = Math.pow(base, exampleExponent)
    const exampleAnswer = exampleResult % 10
    const instructions = `The final digit of $$${base}^{${exampleExponent}}=${exampleResult}$$ is $$${exampleAnswer}$$.`
    const question = `What is the final digit of $$${base}^{${exponent}}$$?`
    return {instructions, question, answer}
  }
}

export class Triangle2 extends QGen {
  static info = {
    name: 'Triangle Problem 2',
    description: 'Easy to answer, hard to prove'
  }
  
  generate() {
    const rd = this.random
    const letters = rd.choice(['ABCD', 'FGHJ', 'JKLM', 'PQRS', 'WXYZ'])
    const [A, B, C, D] = letters
    const question = `Suppose $$${D}$$ is a point on side $$\\overline{${B}${C}}$$ of $$\\triangle{${A}${B}${C}}$$ such that $$${D}${C} = 2${B}${D}$$, $$\\angle{${B}${C}${A}} \\cong \\angle{${B}${A}${D}}$$, and $$m\\angle{${C}${A}${D}} = 30\\degree$$. Determine the measure of $$\\angle{${A}${B}${C}}$$ and justify your answer.`
    const answer = "$$90\\degree$$"
    return {question, answer}
  }
}

export class EightCircles extends QGen {
  static info = {
    name: 'Eight Circles',
    description: 'Hard geometry problem, less hard trigonometry problem'
  }

  static params = {
    numberOfCircles: {
      label: 'number of circles',
      type: 'multiple-select',
      minSelections: 1,
      options: [4,5,6,7,8,9,10]
    },
    given: {
      label: 'given',
      type: 'multiple-select',
      options: ['small radius', 'large radius']
    }
  }
  static defaultParams = {
    numberOfCircles: {8: true},
    given: {'small radius': true}
  }

  generate(params) {
    const rd = this.random
    const n = rd.choice(Object.keys(params.numberOfCircles).filter(k => params.numberOfCircles[k]))
    const given = rd.choice(Object.keys(params.given).filter(k => params.given[k]))
    const radians = 2*Math.PI/n
    const f = 2 * Math.sin((Math.PI - radians)/2) / Math.sin(radians) + 1
    let smallRadius, bigRadius
    if (given === 'small radius') {
      smallRadius = rd.randint(2,9)
      bigRadius = f * smallRadius
    } else {
      bigRadius = rd.randint(4,35)
      smallRadius = bigRadius / f
    }

    let question = `${capitalize(sayinteger(n))} congruent circles are inscribed in a larger circle, as shown in the diagram. Each circle intersects any other circle that it touches at exactly one point.`
    let answer
    if (given === 'small radius') {
      question += ` Given that the radius of each of the smaller circles is ${smallRadius} units, determine the radius of the large circle. Round your answer to four decimal places.`
      answer = round(bigRadius, 4)
    } else {
      question += ` Given that the radius of the large circle is ${bigRadius} units, determine the radius of each of the smaller circles. Round your answer to four decimal places.`
      answer = round(smallRadius, 4)
    }

    // make diagram
    const graphr = 1
    const graphR = f
    const diffR = f - 1
    const graphSize = round(f * 1.05, 2)
    const span = [-graphSize, -graphSize, graphSize, graphSize].map(n => round(n,2)).join(' ')
    
    const unitCircle = []
    for (let i = 0; i < n; i++) {
      const angle = i*radians
      const x = Math.cos(angle)
      const y = Math.sin(angle)
      unitCircle.push({x, y})
    }
    const smallCircles = unitCircle.map(({x, y}) => [
      'Circle', {
        cx: round(diffR*x,4), 
        cy: round(diffR*y,4), 
        r: round(graphr,4)
      }
    ])
    const points = unitCircle.map(({x, y}) => [
      'Point', {x: round(x*graphR,4), y: round(y*graphR,4)}
    ])
    for (let i = 0; i < unitCircle.length; i++) {
      const p1 = unitCircle[i]
      const p2 = unitCircle[(i+1)%unitCircle.length]
      const x = diffR*(p1.x + p2.x)/2
      const y = diffR*(p1.y + p2.y)/2
      points.push(['Point', {x: round(x,4), y: round(y,4)}])
    }

    
    const rotate = rd.randint(0,359)
    const diagram = [
      'CoordinatePlane', {
        span, 
        grid: false, 
        axis: false,
        height: '1.7in', 
        style: 'geometry',
      },
      [
        'Transform', {type: 'rotate', a: rotate},
        ['Circle', {cx: 0, cy: 0, r: round(graphR,4)}],
        ...smallCircles,
        ...points
      ]
    ]
    
    return {
      question,
      diagram,
      answer,
    }
  }
}

export class HiddenIsosceles extends QGen {
  static info = {
    name: 'Triangle Problem 1',
    description: 'Requires no trigonometry'
  }

  generate() {
    const rd = this.random

    let [A, B, C, D, E, F, G] = rd.choice(['ABCDEFG', 'MNOPQRS', 'TUVWXYZ'])
    const r = rd.random()

    if (r < 1/3) {
      [A, B, C, D, E, F, G] = [A, F, D, E, B, G, C]
    } else if (r < 2/3) {
      [A, B, C, D, E, F, G] = [F, A, C, B, E, G, D]
    }
    const angleFAE = rd.randint(41, 62)

    const question = [
      `Given that $$${[B,D].sort().join('')} = ${[C,D].sort().join('')}$$,`,
      `$$${[B,E].sort().join('')} = 2${[A,D].sort().join('')}$$,`,
      `and $$m\\angle{${F}${A}${E}} = ${angleFAE}\\degree$$, determine`,
      `$$m\\angle{${F}${E}${A}}$$.`
    ].join(' ')

    
    const angleC = 65//rd.randint(60,70)
    const angleB1 = 22//rd.randint(20, 30)
    const angleB2 = 15//rd.randint(12, 18)
    const vecCA = Vector.Polar(180-angleC, 1, {degrees: true})
    const vecBE = Vector.Polar(angleB1, 1, {degrees: true})
    const vecBA = Vector.Polar(angleB1 + angleB2, 1, {degrees: true})

    const b = new Point(0,0)
    const c = new Point(12,0)
    const d = b.midpoint(c)
    const lineAB = new Line(b, vecBA)
    const lineAC = new Line(c, vecCA)

    const a = lineAB.intersection(lineAC)
    const e = new Line(b, vecBE).intersection(lineAC)
    const g = new Line(d, vecBE).intersection(lineAC)
    const ad = new Segment(a, d)
    const be = new Segment(b, e)
    const dg = new Segment(d, g)
    const f = ad.toLine().intersection(be.toLine())
    const triangle = new Polygon(a, b, d, c, g, e)

    const span = [b.x - 1, b.y - 1, c.x + 1, a.y + 1].map(n => round(n,2)).join(' ')

    const opts = {
      round: 2, 
      degrees: true, 
      markers: '---',
      labels: [A, B, D, C, G, E].map(l => '$$' + l + '$$'),
      distance: 1
    }
    const labelF = f.add(Point.Polar(
      bisectAngle(a.subtract(d).atan2(), b.subtract(e).atan2()),
      opts.distance, 
      {degrees: false}
    )).toObject(opts)

    const diagram = [
      'CoordinatePlane', 
      {
        span, 
        grid: false, axis: false, 
        style: 'geometry', 
        height: '1.5in'
      },
      ...triangle.plot(opts),
      ...[ad, be, dg].map(seg => seg.plot(opts)),
      ...[a, b, c, d, e, f, g].map(p => p.plot(opts)),
      ['Overlay', labelF, '$$' + F + '$$']
    ]

    return {
      question,
      diagram,
      answer: `$$${angleFAE}\\degree$$`
    }
  }
}


export class KissingCircles extends QGen {
  static info = {
    name: 'Kissing Circles',
  }

  generate() {
    const [A, B, C] = 'ABC'
    const f = this.random.randint(1,2)
    const sqrtb = this.random.choice([2, 3]) * f
    const sqrta = this.random.randint(sqrtb+1, 5) * f
    const a = sqrta*sqrta
    const b = sqrtb*sqrtb
    const c = fraction(a * b, a + 2 * sqrta*sqrtb + b)
    const question = [
      `Circles $$${A}$$, $$${B}$$, and $$${C}$$ all lie tangent to the same`,
      'line and to each other, as shown in the diagram.',
      `The radius of circle $$${A}$$ is $$${a}$$,`,
      `and the radius of circle $$${B}$$ is $$${b}$$.`,
      `Determine the radius of circle $$${C}$$.`
    ].join(' ')
    const answer = `$$${c.toLatex()}$$`

    const graph = {a: 7, b: 4}
    graph.c = (({a, b}) => (a * b) / (a + 2 * Math.sqrt(a * b) + b))(graph)
    graph.ax = 0
    graph.bx = (({a, b}) => Math.sqrt((a+b)*(a+b) - (a-b)*(a-b)))(graph)
    graph.cx = (({a, c}) => Math.sqrt((a+c)*(a+c) - (a-c)*(a-c)))(graph)

    const ptA = new Point(graph.ax, graph.a)
    const ptB = new Point(graph.bx, graph.b)
    const ptC = new Point(graph.cx, graph.c)
    const circleA = new Circle(ptA, graph.a)
    const circleB = new Circle(ptB, graph.b)
    const circleC = new Circle(ptC, graph.c)
    const span = [graph.ax-graph.a - 1, -1, graph.bx+graph.b+1, 2*graph.a+1].map(n => round(n,2))


    const opts = {round: 2}
    const diagram = [
      'CoordinatePlane',
      {span: span.join(' '), axis: false, grid: false, style: 'geometry', height: '1.5in'},
      ['Path', {points: `${span[0]},0 ${span[2]},0`}],
      ...[circleA, circleB, circleC].map(c => c.plot(opts)),
      ['Overlay', {...ptA.toObject(opts)}, '$$' + A + '$$'],
      ['Overlay', {...ptB.toObject(opts)}, '$$' + B + '$$'],
      ['Overlay', {...ptC.toObject(opts)}, '$$' + C + '$$'],
    ]

    return { question, diagram, answer }

  }
}