import {
  OPEN_MODAL,
  CLOSE_MODAL,
  OPEN_POPUP,
  CLOSE_POPUP,
  UPDATE_GENERATOR_SELECTION,
} from '../actions/uistate'

const initialState = {
  generatorSelection: null,
  modal: null,
  popup: null,
}

function uistate(state=initialState, action) {
  switch (action.type) {
    case OPEN_MODAL:
      return {...state, modal: action.payload}
    case CLOSE_MODAL:
      return {...state, modal: null}
    case OPEN_POPUP:
      return {...state, popup: action.payload}
    case CLOSE_POPUP:
      return {...state, popup: null}
    case UPDATE_GENERATOR_SELECTION:
      return {...state, generatorSelection: action.payload}
    default: return state
  }
}

export default uistate