import React from 'react'


export default class Label extends React.Component {
  constructor(props) {
    super(props)
    this.ref = React.createRef()
    this.state = {}
  }

  componentDidMount() {
    const CTM = this.ref.current.getCTM()
    this.setState({ctm: CTM})
  }

  render() {
    let {x, y, anchor="W", rotate} = this.props

    const anglemap = {
      E: 0,
      NE: 45,
      N: 90,
      NW: 135,
      W: 180,
      SW: 225,
      S: 270,
      SE: 315,
      left: 135,
      right: 0,
    }

    const anchorAngle = anglemap[anchor] ? anglemap[anchor] : +anchor % 360

    const tb = anchorAngle === 0 || anchorAngle === 180 ? 'center' : anchorAngle < 180 ? 'top' : 'bottom'
    const lr = anchorAngle < 90 ? 'right' : anchorAngle <= 270 ? 'left' : 'right'  

    const fobj = {
      x: 0, y: 0,  // Chrome rounds foreignObject coordinates to integers, so it works better to apply them later 
      width: '100%', height: '100%',
      style: {overflow: 'visible'}, 
    }
    const outerstyle = {
      width: '0', height: '0',
      margin: '0',
      padding: '0',
      transformOrigin: lr + ' center',
      position: 'relative',
    }
    const innerstyle = {
      position: 'absolute',
      transformOrigin: 'center center',
      transform: 'translate(-50%,-50%)',
      fontSize: '0.1em',
    }
  
    if (anchor) {
      const [transformOrigin, initialTransform] = {
        E: ['right center', 'translate(-100%,-50%)'],
        NE: ['right top', 'translate(-100%,0)'],
        N: ['center top', 'translate(-50%,0)'],
        NW: ['left top', 'translate(0,0)'],
        W: ['left center', 'translate(0,-50%)'],
        SW: ['left bottom', 'translate(0,-100%)'],
        S: ['center bottom', 'translate(-50%,-100%)'],
        SE: ['right bottom', 'translate(-100%,-100%)'],
        center: ['center center', 'translate(-50%,-50%)'],
      }[anchor]
    
      innerstyle.transformOrigin = transformOrigin
      innerstyle.transform = initialTransform
    }
    
    return (
      <foreignObject {...fobj} ref={this.ref} xmlns="http://www.w3.org/1999/xhtml">
        <div style={outerstyle}>
          <div className="svgplot-label" style={innerstyle}>
            {this.props.children}
          </div>
        </div>

      </foreignObject>
    )
  }
}