import React from 'react'
import { Circle as SCircle, Rect as SRect, Ellipse as SEllipse } from './react-svgplot'
import { Style } from '.'

export const Circle = props => {
  const {style, ...otherprops} = props
  if (style) {
    return (
      <Style name={style}><SCircle {...otherprops}/></Style>
    )
  }
  return <SCircle {...otherprops}/>
}

export const Rect = props => {
  const {style, ...otherprops} = props
  if (style) {
    return (
      <Style name={style}><SRect {...otherprops}/></Style>
    )
  }
  return <SRect {...otherprops}/>
}

export const Ellipse = props => {
  const {style, ...otherprops} = props
  if (style) {
    return (
      <Style name={style}><SEllipse {...otherprops}/></Style>
    )
  }
  return <SEllipse {...otherprops}/>
}