import React from 'react'
import { connect } from 'react-redux'
import { updateGeneratorDefaults } from '../../actions/generators'
import styles from './styles.module.css'

class SetGeneratorParams extends React.Component {

  constructor(props) {
    super(props)
    this.state = {defaultParams: props.defaultParams}
    //if (!props.params) {
    //  console.log("There are no options to set for this type of question")
    //  props.onRequestClose()
    //}
  }

  setParam(k, v) {
    const { type } = this.props.params[k]
    if (type === 'multiple-select') {
      const {val, checked} = v
      if (!checked) {
        const {minSelections=0} = this.props.params[k]
        let count = 0
        for (const opt in this.state.defaultParams[k]) {
          if (this.state.defaultParams[k][opt]) {
            count += 1
          }
        }
        if (count <= minSelections) {
          console.log(`${k} must have at least ${minSelections} options selected`)
          return
        }
      }
      this.setState({
        defaultParams: {
          ...this.state.defaultParams, 
          [k]: {
            ...this.state.defaultParams[k],
            [val]: checked,
          }
        }
      })
    }
    else {
      this.setState({defaultParams: {...this.state.defaultParams, [k]: v}})
    }
  }

  renderParamSetter(k) {
    const props = this.props.params[k]
    const paramLabel = props.label === undefined ? k : props.label
    const key = `set-default-parameter-${k}`
    const currentValue = this.state.defaultParams[k]
    if (props.type === 'select') {
      return (
        <div key={key} className={styles.Param + ' ' + styles.ParamSelect}>
          <label htmlFor={key} >{paramLabel}</label>
          <select id={key} value={currentValue}
            onChange={evt => this.setParam(k, evt.target.value)}
          >
            {props.options.map(o => {
              const value = o.value === undefined ? o : o.value
              const label = o.label === undefined ? o : o.label
              return <option key={value} value={value}>{label}</option>
            })}
          </select>
        </div>
      )
    }
    if (props.type === 'boolean') {
      return (
        <div key={key} className={styles.Param + ' ' + styles.ParamBoolean}>
          <label htmlFor={key}>{paramLabel}</label>
          <input id={key} type="checkbox" checked={currentValue}
            onChange={evt => this.setParam(k, evt.target.checked)}
          />
        </div>
      )
    }
    if (props.type === 'multiple-select') {
      return (
        <div key={key} className={styles.Param + ' ' + styles.ParamMultipleSelect}>
          <label htmlFor={key}>{paramLabel}</label>
          {props.options.map(o => {
            const value = o.value === undefined ? o : o.value
            const label = o.label === undefined ? o : o.label
            const current = currentValue[value]
            const optId = `${key}.${value}`
            return (
              <div key={value} className={styles.ParamMultipleSelectOption}>
                <label htmlFor={optId}>{label}</label>
                <input type="checkbox" id={optId}
                  checked={current}
                  onChange={evt => this.setParam(k, {val: value, checked: evt.target.checked})}
                />
              </div>
            )
          })}
        </div>
      )
    }
  }

  submit = () => {
    this.props.onSubmit({id: this.props.id, defaultParams: this.state.defaultParams})
    this.props.onRequestClose()
  }

  render() {
    const paramKeys = []
    for (const k in this.props.params) {
      paramKeys.push(k)
    }
    return (
      <div className={styles.ParamSetter}>
        {paramKeys.map(k => this.renderParamSetter(k))}
        <button onClick={this.submit}>Apply</button>
        <button onClick={this.props.onRequestClose}>Cancel</button>
      </div>
    )
  }
}


const mapStateToProps = state => {
  const id = state.uistate.generatorSelection
  return ({
    id: id,
    params: state.generators.params[id],
    defaultParams: state.generators.defaultParams[id],
  })
}
const mapDispatchToProps = dispatch => ({
  onSubmit: ({id, defaultParams}) => dispatch(updateGeneratorDefaults({id, defaultParams})),
})

export default connect(mapStateToProps, mapDispatchToProps)(SetGeneratorParams)