import React from 'react';
import { connect } from 'react-redux'
import ReactModal from 'react-modal'
import { appendContent, loadDocument, fixPagination } from '../../actions/document'
import { loadStyle } from '../../actions/style'
import { initialState as initialDocumentState } from '../../reducers/document'
import { initialState as initialStyleState } from '../../reducers/style'
import styles from './styles.module.css'


class ImportModal extends React.Component {
  static defaultProps = {
    isOpen: false,
  }

  constructor(props) {
    super(props)
    this.state = {
      importMode: 'new',
      filename: null, 
      validFilename: null, 
      message: null,
      document: null,
      style: null,
    }
  }

  setImportMode = evt => this.setState({importMode: evt.target.value})

  close = () => {
    this.setState({
      filename: null, 
      validFilename: null, 
      message: null,
      document: null,
      style: null,
      dragOver: false,
    })
    this.props.onRequestClose()
  }

  handleDragEnter = evt => {
    evt.stopPropagation()
    evt.preventDefault()
    this.setState({dragOver: true})
  }

  handleDragLeave = evt => {
    evt.stopPropagation()
    evt.preventDefault()
    this.setState({dragOver: false})
  }

  handleDragOver = evt => {
    evt.stopPropagation()
    evt.preventDefault()
    evt.dataTransfer.dropEffect = 'copy'
  }

  handleDrop = evt => {
    evt.stopPropagation()
    evt.preventDefault()
    const file = evt.dataTransfer.files[0]
    this.setState({filename: file.name, dragOver: false})
    this.readFile(file)
    return false
  }

  selectFile = evt => {
    const file = evt.target.files[0]
    this.setState({filename: file.name})
    this.readFile(file)
  }

  readFile(file) {
    if (file) {
      const reader = new FileReader()
      reader.onload = evt => this.checkData(evt.target.result)
      reader.readAsText(file)
    }
  }

  checkData(dataString) {
    let data
    try {
      data = JSON.parse(dataString)
    } catch {
      this.setState({message: 'File is not valid JSON'})
      return
    }
    try {
      let toLoad = {document: null, style: null}
      if (data.document) {
        const document = Object.assign({}, initialDocumentState)
        for (const k in initialDocumentState) {
          if (data.document[k] !== undefined) {
            document[k] = data.document[k]
          }
        }
        toLoad.document = document
      }
      if (data.style) {
        const style = {}
        for (const k in initialStyleState) {
          if (data.style[k] !== undefined) {
            style[k] = data.style[k]
          }
        }
        toLoad.style = style
      }
      this.setState({
        message: '', 
        validFilename: this.state.filename,
        ...toLoad
      })
    } catch(e) {
      console.error(e)
      this.setState({message: 'Error: Unable to load file.', document: null, style: null})
    }
  }

  canLoad = () => {
    if (this.state.importMode === 'new' && (this.state.document || this.state.style)) {
      return true
    }
    if (this.state.importMode === 'append-end' && this.state.document) {
      return true
    }
    if (this.state.importMode === 'append-start' && this.state.document) {
      return true
    }
    if (this.state.importMode === 'style' && this.state.style) {
      return true
    }
    return false
  }

  loadJson = () => {
    if (this.canLoad()) {
      if (this.state.importMode === 'new') {
        if (this.state.document) {
          this.props.loadDocument(this.state.document)
        }
        if (this.state.style) {
          this.props.loadStyle(this.state.style || {})
        } else {
          this.props.fixPagination()
        }
        this.close()
      } 
      else if (this.state.importMode === 'append-end') {
        this.props.appendContent({...this.state.document, where: 'end'})
        this.close()
      }
      else if (this.state.importMode === 'append-start') {
        this.props.appendContent({...this.state.document, where: 'start'})
        this.close()
      }
      else if (this.state.importMode === 'style') {
        this.props.loadStyle(this.state.style)
        this.close()
      }
    } else {
      this.setState({message: 'No valid file selected'})
    }
  }

  load = () => {
    this.loadJson()
  }

  render() {
    return (
      <ReactModal
        isOpen={this.props.isOpen}
        onRequestClose={this.close}
        style={{
          content: {
            //background: 'none',
            top: '0',
            bottom: '0',
            left: '0',
            right: '0',
            borderRadius: 0,
            //margin: '5mm',
            padding: '5mm',
          },
        }}
      >
        <div className={styles.Import}>
          <div>
            <select value={this.state.importMode} onChange={this.setImportMode}>
              <option value="new">Import as new assignment</option>
              <option value="append-end">Import questions only (add to end)</option>
              <option value="append-start">Import questions only (add to beginning)</option>
              <option value="style">Import style only</option>
            </select>
            <div style={{height: '5mm'}}/>
            <button onClick={this.load} disabled={!this.canLoad()}>Import</button>
            <button onClick={this.close}>Cancel</button>
            <div style={{height: '5mm'}}/>
          </div>
          <div 
            className={this.state.dragOver 
              ? styles.ImportDragbox + ' ' + styles.ImportDragboxDragover 
              : styles.ImportDragbox
            }
            onDrop={this.handleDrop}
            onDragOver={this.handleDragOver}
            onDragEnter={this.handleDragEnter}
            onDragLeave={this.handleDragLeave}
            //onClick={this.close}
          >

              <span style={{
                fontSize: '2em', 
                fontWeight: 'bold', 
              }}>Drop File Here</span>
              <div style={{height: '1em'}}/>
              <span>{this.state.filename}</span>
              <div style={{height: '1em'}}/>
              <span style={{color: 'red', fontWeight: 'bold', fontSize: '1.5em'}}>{this.state.message || ''}</span>
              <div style={{height: '1em'}}/>

          </div>
        </div>
      </ReactModal>
    )
  }
}

const mapDispatchToProps = dispatch => ({
  fixPagination: () => dispatch(fixPagination()),
  loadDocument: doc => dispatch(loadDocument(doc)),
  loadStyle: style => dispatch(loadStyle(style)),
  appendContent: append => dispatch(appendContent(append)),
})

export default connect(null, mapDispatchToProps)(ImportModal)