export const ADD_GENERATOR = 'ADD_GENERATOR'
export const UPDATE_GENERATOR_DEFAULTS = 'UPDATE_GENERATOR_DEFAULTS'


export const addGenerator = ({id, generator, info, params, defaultParams}) => ({
  type: ADD_GENERATOR,
  payload: {id, generator, info, params, defaultParams}
})

export const updateGeneratorDefaults = ({id, defaultParams}) => ({
  type: UPDATE_GENERATOR_DEFAULTS,
  payload: {id, defaultParams}
})