const VGAP = 1
const HGAP = 1
const NODERADIUS = 1

export function drawCircuit(circuit, opts={}) {
  const {width, height, above, below} = circuit.requiredSpace()
  return [
    'CoordinatePlane', 
    {
      span: [-1, -below-1, width+1, above+1], 
      axis: true, 
      grid: false, 
      ...opts,
    },
    ['Point', {x: 0, y: 0}],
    ...circuit.draw({start: {x:0,y:0}})
  ]
}

export class Node {
  constructor(label) {
    this.label = label
  }

  requiredSpace() {
    return {
      width: 2*NODERADIUS, 
      height: 2*NODERADIUS, 
      above: NODERADIUS, 
      below: NODERADIUS,
    }
  }

  draw({start={x:0, y:0}, availableWidth}={}) {
    const {x, y} = start
    const extra = availableWidth ? (availableWidth - this.requiredSpace().width)/2 : 0
    if (extra > 0) {
      return [
        ['Path', {points: `${x},${y} ${x+extra},${y}`}],
        ['Circle', {cx:x+extra+NODERADIUS, cy: y, r: NODERADIUS}],
        ['Path', {points: `${x+extra+2*NODERADIUS},${y} ${x+2*extra+2*NODERADIUS},${y}`}]
      ]
    } else {
      return [['Circle', {cx:x+NODERADIUS, cy: y, r: NODERADIUS}]]
    }
  }
}

export class And {
  constructor(...args) {
    this.args = args
  }

  requiredSpace() {
    let w = 0, a = 0, b = 0, h = 0
    for (const x of this.args) {
      const req = x.requiredSpace()
      w += req.width
      if (req.height > h) {
        h = req.height
      }
      if (req.above > a) {
        a = req.above
      }
      if (req.below > b) {
        b = req.below
      }
    }
    w += (this.args.length+1)*HGAP
    return {width: w, height: h, above: a, below: b}
  }

  draw({start={x:0, y:0}, availableWidth}={}) {
    let {x, y} = start
    const extra = availableWidth ? availableWidth - this.requiredSpace().width : 0
    const between = HGAP + extra / (this.args.length+1)

    const lines = [
      ['Path', {points: [[x, y], [x+between, y]]}]
    ]
    x += between

    let parts = []
    for (const a of this.args) {
      parts = parts.concat(a.draw({start: {x, y}}))
      x += a.requiredSpace().width
      lines.push(['Path', {points: [[x,y], [x+between, y]]}])
      x += between
    }

    return [...lines, ...parts]
  }
}

export class Or {
  constructor(...args) {
    this.args = args
  }

  requiredSpace() {
    let w = 0, h = 0
    for (const a of this.args) {
      const req = a.requiredSpace()
      if (req.width > w) {
        w = req.width
      }
      h += req.height
    }
    h += (this.args.length - 1)*VGAP
    return {width: w, height: h, above: h/2, below: h/2}
  }

  draw({start={x: 0, y: 0}, availableWidth}={}) {
    let {x, y} = start

    const requiredSpace = this.requiredSpace()
    const ys = []
    y += requiredSpace.above
    for (const a of this.args) {
      const req = a.requiredSpace()
      y -= req.above
      ys.push(y)
      y -= req.below
      y -= VGAP
    }
    const lines = [[
      ['Path', {
        points: [
          [x, ys[0]], 
          [x, ys[ys.length-1]],
        ],
      }],
      ['Path', {
        points: [
          [x+requiredSpace.width, ys[0]], 
          [x+requiredSpace.width, ys[ys.length-1]],
        ],
      }],
    ]]

    let parts = []
    for (let i = 0; i < this.args.length; i++) {
      parts = parts.concat(this.args[i].draw({start: {x, y: ys[i]}, availableWidth: requiredSpace.width}))
    }

    return [...lines, ...parts]
  }
}