import React from 'react'
import ReactModal from 'react-modal'
import { connect } from 'react-redux'
import { PracticeMode } from '../Presentation'
import styles from './styles.module.css'


const PracticeModal = props => (
  <ReactModal
    isOpen={props.isOpen}
    onRequestClose={props.onRequestClose}
    style={{
      content: {
        border: 'none',
        borderRadius: 0,
        top: 0, bottom: 0, left: 0, right: 0,
        margin: 0,
        padding: 0,
        backgroundColor: 'var(--ui-page)',
      }
    }}
  >
    <PracticeMode {...props}/>
  </ReactModal>
)


const mapStateToProps = state => {
  const id = state.uistate.generatorSelection
  return ({
    generator: state.generators.generators[id],
    defaultParams: state.generators.defaultParams[id],
  })
}

export default connect(mapStateToProps)(PracticeModal)