import {
  ADD_GENERATOR,
  UPDATE_GENERATOR_DEFAULTS,
} from '../actions/generators'

const initialState = {
  generators: {},
  info: {},
  params: {},
  defaultParams: {},
}

const generators = function(state=initialState, action) {
  switch(action.type) {
    case ADD_GENERATOR:
      const {id, generator, info, params, defaultParams} = action.payload
      return {
        ...state, 
        generators: {...state.generators, [action.payload.id]: action.payload.generator},
        info: {...state.info, [action.payload.id]: action.payload.info},
        params: {...state.params, [action.payload.id]: action.payload.params},
        defaultParams: {...state.defaultParams, [action.payload.id]: action.payload.defaultParams}
      }
    case UPDATE_GENERATOR_DEFAULTS:
      return {
        ...state, 
        defaultParams: {
          ...state.defaultParams, 
          [action.payload.id]: {
            ...state.defaultParams[action.payload.id], 
            ...action.payload.defaultParams,
          },
        }
      }
    default: return state
  }
}

export default generators